export default {
  props: {
    songs: {},
    isRank: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    _selectSong (song, index) {
      this.$emit('select', song, index)
    },
    _showRank (index) {
      if (this.isRank && index >= 3) {
        return index + 1
      }
    }
  }
}



