
import { getRecommend } from '../../common/api/recommmend'

import Swiper from 'vux/src/components/swiper/swiper.vue'

import scroll from '@/base/scroll/scroll'
import loading from '@/base/loading/loading'
import {playlistMixin} from '../../common/js/mixin'
export default {
mixins: [playlistMixin],
data () {
return {
recommendData: {}
}
},
computed: {
_swiperData () {
let arr = []
this.recommendData.slider.forEach((item, index) => {
let obj = {}
obj.url = item.linkUrl
obj.img = item.picUrl
arr.push(obj)
})
return arr
}
},
created () {
this._getRecommend()
},
components: {
Swiper,
scroll,
loading
},
methods: {
_getRecommend () {
getRecommend().then(data => {
this.recommendData = data.data
})
},
handlePlaylist () {
let bottom
this.playlist.length > 0 ? bottom = '60px' : bottom = ''
this.$refs.recommend.style.bottom = bottom
},
jump (song) {
let url = `https://y.qq.com/w/taoge.html?ADTAG=myqq&from=myqq&channel=10007100&id=${song.id}`
window.open(url, '_blank')
}
}




}



