import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters(['searchHistory'])
  },
  methods: {
    deleteOne (query) {
      this.$emit('delete', query)
    },
    select (query) {
      this.$emit('select', query)
    }
  }
}



