import {throttle} from '@/common/js/utils'
export default {
  props: {
    placeHolder: {
      type: String,
      default: '搜索歌曲、歌手'
    }
  },
  data () {
    return {
      query: ''
    }
  },
  methods: {
    _clearQuery () {
      this.query = ''
    },
    setQuery (query) {
      this.query = query
    }
  },
  created () {
    this.$watch('query', throttle((newval) => {
      this.$emit('queryChange', newval)
    }, 500))
  }
}



