export default {
  props: {
    delay: {
      type: Number,
      default: 2000
    }
  },
  data () {
    return {
      isShow: false,
      timer: null
    }
  },
  methods: {
    show () {
      clearTimeout(this.timer)
      this.isShow = true
      this.timer = setTimeout(() => {
        this.hide()
      }, this.delay)
    },
    hide () {
      this.isShow = false
    }
  }
}



